
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400;500;600;700&display=swap');


html{
    --bg: #f7f7fa;
    --section: #fcfcfc;
    
    --text1: rgba(22, 22, 22, 0.87);
    --text2: rgba(0, 0, 0, .5);
    --textInvert: rgba(233, 233, 233);
    --accent: #3763FE;
    --danger: #e5534b;
    --accentLight: #E7E9FB;
    --border: rgba(0,0,0,.1);
    --borderLight: #e9ebf0;
    --br10: 6px;
    --br: 15px;

    --awaitingDispatch: lightgreen;
}


    
html.dark {
    --text1: rgb(233, 233, 233);
    --text2: #919191;
    --textInvert: rgb(0, 0, 0);
    --bg: rgb(7, 7, 8);
    --section: rgb(21, 21, 26);

    --accent: #6989fc;
    --accentLight: #43435c;
    --border: rgba(255,255,255,.10);
    --borderLight: rgba(255,255,255,.09);

    --awaitingDispatch: mediumaquamarine;
    }
